@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Inconsolata', monospace;
}
html {
  scroll-behavior: smooth;
}
h1 {
  font-size: 6em;
  margin-top: 0;
}
h3 {
  font-size: 2em;
}
#header {
  background-image: url(/static/media/background.03ea1f47.jpeg);
  margin-top: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
}
.title h3{
  margin: 0;
}
#title-name {
  display: flex;
  margin-top: 10%;
  margin-left: 10%;
}
.title {
  margin-bottom: 1%;
  display: flex;
  justify-content: flex-end;
}
.title-software {
  margin-top: 3%;
  margin-right: 30%;
}
.title-web {
  margin-right: 25%;
}
.title-tech {
  margin-right: 12%;
}
#nav {
  display: flex;
  justify-content: space-around;
  margin: 20px 25%;
}
#nav a {
  font-size: 1.5em;
  color: white;
  text-decoration: none;
}
#nav a:hover {
  text-decoration: overline;
}

#about h3 {
  margin: 0;
  padding-top: 5%;
}
#profile-pic {
  margin: 5% 0 5% 0;
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 100%;
}
#about {
  text-align: center;
}
#about p {
  font-size: 1.3em;
  width: 50%;
  padding-left: 25%;
}
#skill-section h3 {
  padding-bottom: 1%;
}
#skill-pics {
  display: grid;
  grid-template-columns: 20% 20% 20% 20% 20%;
  grid-template-rows: 50% 50%;
}
.icon img {
  max-height: 100px;
  height: auto;
  width: auto;
}
.icon:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  -webkit-transition: -webkit-transform 200ms ease-in-out;
  transition: -webkit-transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
  font-weight: 500;
  text-shadow: 0 2px 2px gray;
}

#portfolio-section {
  display: grid;
  grid-template-rows: 350px 350px 350px;
  grid-template-columns: 2fr 2fr;
}
#portfolio h3 {
  font-size: 2em;
}
.website {
  margin: 10px 30px;
}
.hover-box {
  display: none;
  margin: 10% 30% 0 30%;
  opacity: 1;
  background-color: black;
  border: 1px solid white;
  border-radius: 5%;
  color: white;
  padding-bottom: 3%;
}
.hover-box a {
  color: white;
  margin: 0 10px;
}
.website:hover {
  box-shadow: 0 0 10px;
}
.website:hover .hover-box {
  display: block;
}
.web-macro {
  background-image: url(/static/media/macro.c3fa4ef7.png);
  background-size: contain;
}
.web-events {
  background-image: url(/static/media/events.93384274.png);
  background-size: contain;
}
.web-barrel {
  background-image: url(/static/media/barrel.967f9154.png);
  background-size: contain;
}
.web-caracas {
  background-image: url(/static/media/caracas.071a07ba.png);
  background-size: contain;
}
.web-curtain {
  background-image: url(/static/media/curtain.3720fc60.png);
  background-size: contain;
}

#contact h3 {
  font-size: 2em;
}
#contact-links {
  display: flex;
  justify-content: space-around;
  padding-bottom: 2%;
  border-bottom: 3px solid gray;
  margin: 5% 5%;
}
#contact-links a {
  font-size: 1.2em;
  color: black;
}

